import { useEffect, useState } from "react";
import { useForm } from "react-hook-form";
import { useNavigate } from "react-router-dom";
import { authApi } from "../../../utils/authApi";
import { pattern } from "../../../utils/validations";
import { BaseButton } from "../../BaseButton/BaseButton";
import { BaseForm } from "../../BaseForm/BaseForm";
import "../auth_style.css";
import CloseIcon from "@mui/icons-material/Close";
import VisibilityIcon from "@mui/icons-material/Visibility";
import VisibilityOffIcon from "@mui/icons-material/VisibilityOff";
import { openNotification } from "../../Notification/Notification";

export const SignIn = ({ setShowModal }) => {
  // useState    
  const [type, setType] = useState(false);
  
  //  
  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm({ mode: "onSubmit" });
  
  // useNavigate
  const navigate = useNavigate();
  
  //     
  const handleClick = (e) => {
    e.preventDefault();
    navigate("/sign-up");
  };

  //    
  const sendData = async (data) => {
    try {
      const res = await authApi.signIn(data);
      localStorage.setItem("token", res.token);
      navigate("/");
    } catch (error) {
      openNotification("error", "", "   ");
    }
  };
  // Register  email
  const emailRegister = register("email", {
    required: "E-mail ",
  });
  // Register  
  const passwordRegister = register("password", {
    required: " ",
    pattern,
  });

  // useEffect    
  useEffect(() => {
    setShowModal(true);
  }, [setShowModal]);

  // 
  return (
    <>
      <div className="form_close_butn_wrapper">
        <button
          className="form_close_btn"
          title=""
          onClick={() => {
            setShowModal(false);
            navigate("/not_authenticated");
          }}
        >
          <CloseIcon />
        </button>
      </div>
      <BaseForm submitForm={handleSubmit(sendData)} title={""}>
        <div className="auth__controls">
          <input
            type="text"
            {...emailRegister}
            placeholder="Email"
            className="auth__input"
          />
          {errors?.email && (
            <span className="auth__warning">{errors.email?.message}</span>
          )}
          <input
            type={type ? "text" : "password"}
            {...passwordRegister}
            placeholder=""
            className="auth__input"
          />
          {errors?.password && (
            <span className="auth__warning">{errors.password?.message}</span>
          )}
          {!errors?.password && (
            <span className="auth__eye_register" onClick={() => setType(!type)}>
              {type ? <VisibilityOffIcon /> : <VisibilityIcon />}
            </span>
          )}

          <span
            className="auth__info auth__link"
            onClick={() => navigate("/change-password")}
          >
             
          </span>

          <div className="auth__actions">
            <BaseButton type="submit" color={"yellow"}>
              <span></span>
            </BaseButton>
            <BaseButton onClick={handleClick} color={"white"}>
              <span></span>
            </BaseButton>
          </div>
        </div>
      </BaseForm>
    </>
  );
};
