import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import { openNotification } from "../../components/Notification/Notification";

// createAsyncThunk -  (action),     (1)   (2),  .
//  :   -  ,  / -  
// 1-  -  
// 2-  -   

//  
export const getUser = createAsyncThunk(
  "user/getUser",
  async function (
    dataOutside,
    { getState, fulfillWithValue, rejectWithValue, extra: api }
  ) {
    try {
      const data = await api.getUserInfo();
      return fulfillWithValue(data);
    } catch (error) {
      return rejectWithValue(error);
    }
  }
);
//   
export const changeUserData = createAsyncThunk(
  "user/changeUserData",
  async function (
    dataOutside,
    { getState, rejectWithValue, fulfillWithValue, extra: api }
  ) {
    try {
      const data = dataOutside.avatar
        ? await api.updateAvatar(dataOutside)
        : await api.updateUserInfo(dataOutside);
      openNotification(
        "success",
        "",
        dataOutside.avatar
          ? "a  "
          : "  "
      );
      return fulfillWithValue(data);
    } catch (error) {
      openNotification("error", "", "    / ");
      return rejectWithValue(error);
    }
  }
);

const isError = (action) => {
  return action.type.endsWith("rejected");
};

//  
const initialState = {
  data: {},
  loading: true,
  error: null,
  test: undefined,
};
//    
const userSlice = createSlice({
  name: "user",
  initialState,
  reducers: {},
  //    
  extraReducers: (builder) => {
    //  
    builder.addCase(getUser.pending, (state, action) => {
      state.loading = true;
    });
    builder.addCase(getUser.fulfilled, (state, action) => {
      state.data = action.payload;
      state.loading = false;
    });
    //   
    builder.addCase(changeUserData.fulfilled, (state, action) => {
      state.data = action.payload;
      state.loading = false;
    });
    builder.addMatcher(isError, (state, action) => {
      state.error = action.payload;
      state.loading = false;
      openNotification("error", "error", " ");
    });
  },
});

export default userSlice.reducer;
